/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #303E47;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: whitesmoke;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #7fffd4;
}